<template>
  <div class="page-content update-project">
    <el-button class="page-back" icon="el-icon-arrow-left" circle @click="pageBack" size="small"></el-button>
    <el-tabs v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="" name="0">
        <el-form ref="infoForm" :model="form" label-width="200px" style="padding-bottom: 12px;">
          <!-- <el-row type="flex" class="row-bg" justify="center">
            <el-col :span="8">
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.brandName">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8"></el-col>
          </el-row> -->
          <el-row type="flex" class="row-bg" justify="center">
            <el-col :span="8">
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-cascader :options="cityInfo" placeholder="()//" v-model="selectedOptions" :props="props" :change-on-select="true" :clearable="true" @change="handleChange">
                </el-cascader>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-select v-model="form.brandName">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-select v-model="form.projectAttribute">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-select v-model="form.storeAttribute">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.builtArea">
                  <template slot="append"></template>
                </el-input>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.depositStandard">
                  <template slot="append"></template>
                </el-input>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-date-picker type="date" placeholder="" value-format="yyyy-MM-dd" v-model="form.orderAcceptTime">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-date-picker type="date" placeholder="" value-format="yyyy-MM-dd" v-model="form.measuringTime">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-date-picker type="date" placeholder="" value-format="yyyy-MM-dd" v-model="form.predictStartTime">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-select v-model="form.customerAttribute">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.customerName"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.customerPhone"></el-input>
              </el-form-item>
              <el-form-item label="QQ" :rules="[{ required: true, message: 'QQ', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.customerQq"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.address"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.firstPartManagerName"></el-input>
              </el-form-item>
              <el-form-item label="()" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.firstPartManagerPhone"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                <el-input placeholder="" v-model="form.mallManagerName"></el-input>
              </el-form-item>
              <el-form-item label="()" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.mallManagerPhone"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.orderPersonnelName"></el-input>
              </el-form-item>
              <el-form-item label="()" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.orderPersonnelPhone"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.measuringStaffName"></el-input>
              </el-form-item>
              <el-form-item label="()" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.measuringStaffPhone"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.designDepartment"></el-input>
              </el-form-item>
              <el-form-item label="" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.designDepartmentManagerName"></el-input>
              </el-form-item>
              <el-form-item label="()" :rules="{ required: true, message: '', trigger: 'blur' }">
                <el-input placeholder="" v-model="form.designDepartmentManagerPhone"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-alert class="info-form-alert" title="" :description="alertTxt" center type="info" :closable="false">
          </el-alert>
          <!--  add -->
          <el-form-item class="form-bottom-toolbar" v-if="!projectId" label-width="0px">
            <el-button type="primary" @click="newProjectInfo('infoForm')" size="medium"></el-button>
            <el-button @click="cancelPanel" size="medium"></el-button>
          </el-form-item>
          <el-form-item class="form-bottom-toolbar" v-if="projectId" label-width="0px">
            <el-button type="primary" @click="modifyInfo" size="medium"></el-button>
            <el-button @click="cancelPanel" size="medium"></el-button>
          </el-form-item>

        </el-form>
      </el-tab-pane>
      <el-tab-pane label="" :disabled="!projectId" name="1">
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="8">
            <div class="project-info">
              <span></span>
              <span>{{projectGrid.projectName}}</span>
            </div>

            <div class="project-info">
              <span></span>
              <span>{{projectGrid.projectNumber}}</span>
            </div>
          </el-col>

          <el-col :span="8">
            <div class="project-info">
              <span></span>
              <span>{{projectGrid.address}}</span>
            </div>
          </el-col>
        </el-row>

        <el-table :data="projectGrid.itemList" border style="width: 100%" height="400" :summary-method="getSummaries" show-summary v-if="!isNextTable">
          <el-table-column label="" type="index" width="56">
          </el-table-column>
          <el-table-column prop="itemName" label="">
            <template slot-scope="scope">
              <span v-if="scope.$index === projectGrid.itemList.length-1">
                
              </span>
              <el-button v-else @click="moveToNext(scope.$index, scope.row)" type="text">
                {{scope.row.itemName}}
              </el-button>
            </template>

          </el-table-column>
          <el-table-column prop="costMoney" label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <el-table :data="logoProjectTable" style="width: 100%" max-height="700px" :span-method="arraySpanMethod" sum-text="" show-summary v-show="isNextTable">
          <el-table-column label="" width="56">
            <template slot-scope="scope">
              <el-button type="text" icon="el-icon-plus" @click="newTableRow(scope.$index)"></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="applyPart" label="" width="150">
            <template slot-scope="scope">
              <el-select v-model="scope.row.applyPart">
                <el-option v-for="apply in applyPartEnums" :key="apply.name" :label="apply.name" :value="apply.name"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="response" label="" width="150">
            <template slot-scope="scope">
              <el-select v-model="scope.row.response">
                <el-option v-for="response in responseEnums" :key="response.name" :label="response.name" :value="response.name"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="fontName" label="" width="150">
            <template slot-scope="scope">
              <el-select v-model="scope.row.fontName">
                <el-option v-for="fontName in fontNameEnums" :key="fontName.name" :label="fontName.name" :value="fontName.name"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="wordThickness" label="" width="80">
          </el-table-column>
          <el-table-column prop="borderCraft" label="" width="110">
          </el-table-column>
          <el-table-column label="LOGO">
            <el-table-column prop="logoCount" label="" width="80">
            </el-table-column>
            <el-table-column label="" width="80">
            </el-table-column>
            <el-table-column prop="logoUnitPrice" label="" width="80">
            </el-table-column>
            <el-table-column prop="logoCostSummary" label="" width="80">
            </el-table-column>
          </el-table-column>

          <el-table-column label="">
            <el-table-column prop="powerModel" label="" width="150">
              <template slot-scope="scope">
                <el-select v-model="scope.row.powerModel">
                  <el-option v-for="powerModel in powerModelEnums" :key="powerModel.name" :label="powerModel.name" :value="powerModel.name"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="powerCount" label="" width="80">
            </el-table-column>
            <el-table-column label="">
            </el-table-column>
            <el-table-column prop="powerUnitPrice" label="" width="80">
            </el-table-column>
            <el-table-column prop="powerCostSummary" label="" width="80">
            </el-table-column>
          </el-table-column>

          <el-table-column label="">
            <el-table-column label="" width="80">
            </el-table-column>
            <el-table-column prop="installationUnitPrice" label="" width="80">
            </el-table-column>
            <el-table-column prop="installationCostSummary" label="" width="80">
            </el-table-column>
          </el-table-column>
        </el-table>

        <div v-show="isNextTable" class="form-bottom-toolbar" style="line-height: 59px;">
          <el-button type="primary" @click="submitTable" size="medium"></el-button>
          <el-button @click="cancelLogoTable" size="medium"></el-button>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import _ from 'lodash'
import cityData from '@/libs/city-data'
import proxy from './proxy'

const initTableItem = {
  'applyPart': '',
  'borderCraft': '-',
  'fontName': '',
  'installationCostSummary': 0,
  'installationUnitPrice': 0,
  'logoCostSummary': 0,
  'logoCount': 0,
  'logoUnitPrice': 0,
  'powerCostSummary': 0,
  'powerCount': 0,
  'powerModel': '',
  'powerUnitPrice': 0,
  'response': '',
  'wordThickness': 0
}
export default {
  name: 'UpdateInfo',
  data () {
    return {
      alertTxt: '',
      activeName: 0,
      cityInfo: cityData,
      selectedOptions: [], // 
      form: {
        province: '', city: '', county: '', address: '', builtArea: 0, brandName: '', customerAttribute: '', customerName: '', customerPhone: '', customerQq: '', depositStandard: 0, designDepartment: '', designDepartmentManagerName: '', designDepartmentManagerPhone: '', firstPartManagerName: '', firstPartManagerPhone: '', mallManagerName: '', mallManagerPhone: '', measuringStaffName: '', measuringStaffPhone: '', measuringTime: '', orderAcceptTime: '', orderPersonnelName: '', orderPersonnelPhone: '', predictStartTime: '', projectAttribute: '', storeAttribute: ''
      },
      originKeys: [],
      // 
      props: {
        value: 'label',
        children: 'children'
      },

      // 
      projectGrid: {
        projectName: '',
        address: '',
        projectNumber: '',
        itemList: [],
        tax: 0,
        total: 0
      },
      isNextTable: false,
      applyPartEnums: [],
      responseEnums: [],
      fontNameEnums: [],
      powerModelEnums: [],
      logoProjectTable: []
    }
  },
  computed: {
    projectId () {
      if (this.$route.params.id && this.$route.params.id !== 'add') {
        return this.$route.params.id
      } else {
        return ''
      }
    }
  },
  watch: {
    activeName (nActive) {
      if (nActive === '1') {
        this.getCostById()
      }
    }
  },
  methods: {
    pageBack () {
      this.$router.push({ name: 'Guide' })
    },
    // Tab 
    tabClick (e) { },
    // 
    getCostById () {
      proxy.getCostDetailById(this.projectId).then(res => {
        let resData = res.data
        // console.log(resData)
        Object.assign(this.projectGrid, resData)
        this.projectGrid.itemList.push({
          itemName: '',
          costMoney: resData.tax
        })
      })
      // this.projectGrid.itemList.push({name: '', price: 99})
    },
    // 
    getProjectById () {
      proxy.getDetailById(this.projectId).then(res => {
        let resData = res.data
        Object.assign(this.form, resData)
        this.form = _.pick(resData, this.originKeys)
        // console.log(this.form)

        this.selectedOptions = [this.form.province, this.form.city, this.form.county]
      })
    },
    // 
    newProjectInfo (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          proxy.addProject(this.form).then(res => {
            // console.log(res.data.ret)
            this.$router.push({ name: 'Guide' })
          })
        } else {
          return false
        }
      })
    },
    modifyInfo () {
      proxy.modifyProject(this.projectId, this.form).then(res => {
        // console.log(res.data)
        this.$message({
          message: '',
          type: 'success'
        })
      })
    },
    cancelPanel () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$router.push({ name: 'Guide' })
      }).catch(() => { })
    },
    // 
    handleChange (value) {
      this.form.province = this.selectedOptions[0]
      this.form.city = this.selectedOptions[1]
      this.form.county = this.selectedOptions[2]
    },
    // 
    getSummaries (param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = ''
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          sums[index] += ''
        } else {
          sums[index] = ''
        }
      })

      return sums
    },

    arraySpanMethod ({ row, column, rowIndex, columnIndex }) {
    },

    // 
    moveToNext (index, data) {
      this.isNextTable = true
      this.getEnum()
      this.getLogoProject()
    },

    getEnum () {
      proxy.getAttributeEnum('applyPart').then(res => {
        this.applyPartEnums = res.data
      })
      proxy.getAttributeEnum('response').then(res => {
        this.responseEnums = res.data
      })
      proxy.getAttributeEnum('fontName').then(res => {
        this.fontNameEnums = res.data
      })
      proxy.getAttributeEnum('powerModel').then(res => {
        this.powerModelEnums = res.data
      })
    },

    newTableRow (index) {
      this.logoProjectTable.splice(index + 1, 0, Object.assign({projectId: this.projectId}, initTableItem))
    },
    // logo
    getLogoProject () {
      proxy.getAllLogoProject(this.projectId).then(res => {
        this.logoProjectTable = res.data
        if (!res.data || res.data.length === 0) {
          this.logoProjectTable.push(Object.assign({projectId: this.projectId}, initTableItem))
        } else {
          this.logoProjectTable = res.data
        }
      })
    },

    // LOGO
    submitTable () {
      // console.log(this.logoProjectTable)
      proxy.saveLogoProjects(this.logoProjectTable).then(res => {
        this.$message({
          message: 'LOGO',
          type: 'success'
        })
        this.isNextTable = false
        this.getCostById()
      })
    },
    cancelLogoTable () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.isNextTable = false
      }).catch(() => { })
    }
  },
  mounted () {
    this.originKeys = Object.keys(this.form)
    if (this.projectId) {
      this.getProjectById()
    }
  }
}
</script>



  


